import { useContext, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import styled from 'styled-components';
import { Nav } from 'react-bootstrap';
import { useDispatch } from 'react-redux';
import { addCart } from '../store/store';

// import { Context1 } from './../App.js';   

// let Btn = styled.button`
//     background: ${ props => props.bg };
//     color: ${ props => props.bg == 'blue' ? 'white' : 'black' };
//     padding: 10px;
// `
// let newBtn = style.button(Btn)`

// `

function Detail(props) {

    let dispatch = useDispatch();
    // let { stock } = useContext(Context1);

    let { id } = useParams();
    let shoesImg = parseInt(id) + 1;
    let [discount, setDiscount] = useState(true);
    let [count, setCount] = useState(2);
    let [amount, setAmount] = useState('');
    let [alert, setAlert] = useState(false);
    let [ tab, setTab ] = useState(0);
    let [ fade, setFade ] = useState('');

    useEffect(()=>{
        let getItem = JSON.parse(localStorage.watched);
        getItem.push(id);
        getItem = new Set(getItem);
        getItem = Array.from(getItem);
        localStorage.setItem('watched', JSON.stringify(getItem));
    }, [])

    // let timer = setInterval(() => {
    //     count--;
    //     setCount(count);
    // }, 1000)
    
    // console.log(count);

    //    
    useEffect(() => {

        let a = setTimeout(()=>{
            setFade(('end'));
        }, 200)

        let timer2 = setTimeout(() => {
            setDiscount(false);
            // clearInterval(timer);         
        }, 2000)

        return() => {
            //useEffect    ,   (clean up function)
            setFade('');
            clearTimeout(timer2, a);
        };
    }, [ ]); // [] mount 1 ,     state   

    useEffect(() => {
        if (isNaN(amount)) {
            setAlert(true);
        } else {
            setAlert(false);
        }
    }, [amount])


    return (
        <div className={ 'container start' + fade } style={{transition : 'opacity 0.5s'}}>
            {
                discount ?
                    <div className="alert alert-warning">
                        {count}    
                    </div>
                    : null
            }
            <div className="row">
                <div className="col-md-6">
                    <img src={'https://codingapple1.github.io/shop/shoes' + shoesImg + '.jpg'} width="100%" />
                </div>
                <div className="col-md-6">
                    <h4 className="pt-5">{props.shoes[id].title}</h4>
                    <p>{props.shoes[id].content}</p>
                    <p>{props.shoes[id].price}</p>
                    {
                        alert ? <p>:  </p> : null
                    }
                    <input
                        placeholder=" "
                        onChange={(e) => {
                            setAmount(e.target.value);
                        }}
                    />
                    <button 
                        className="btn btn-danger"
                        onClick={()=>{
                            dispatch(addCart(props.shoes[id]));
                        }}
                    ></button>
                </div>
            </div>
            <Nav fill variant="tabs" defaultActiveKey="link0" style={{ marginTop: "50px" }}>
                <Nav.Item>
                    <Nav.Link eventKey="link0" onClick={()=>{ setTab(0) }}> </Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="link1" onClick={()=>{ setTab(1) }}> </Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="link2" onClick={()=>{ setTab(2) }}></Nav.Link>
                </Nav.Item>
            </Nav>
            <TabContent tab={tab} />
        </div>
    );

}

function TabContent({tab}){

    let [ fade, setFade ] = useState('');

    useEffect(()=>{
        let a = setTimeout(()=>{ setFade('end'); }, 100)
        return() => { 
            clearTimeout(a);
            setFade('');
        }
    }, [ tab ])

   return  (
    <div className={ 'start ' + fade }>
        {[<div>0</div>, <div>1</div>, <div>2</div>][tab]}
    </div>
   );
}


export default Detail;