import { Table } from "react-bootstrap";
import { memo, useState } from "react"
import { useSelector, useDispatch } from 'react-redux';
import { changeName, increase } from '../store/userSlice.js';
import { upCount, delCart } from '../store/store';



// memo)    props   
// let Child = memo(function(){
//     console.log('');
//     return <div></div>
// })


// function (){ 
//     return  10  ;
// }


function Cart() {

    let state = useSelector((state) =>  state )
    let dispatch = useDispatch()  // store.js  
    let [count, setCount] = useState(0);

    // useMemo)    1 . useEffect 
    // useMemo   , useEffect HTML     .
    // let result = useMemo(()=>{ return ()}, [])

    return (
        <div>
            {/* <Child></Child>
            <button onClick={()=>{ setCount(count++) }}> + </button> */}
            <p>{state.user.name}{state.user.age} </p>
            <Table>
                <thead>
                    <tr>
                        <th>#</th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    { 
                        state.cart.map((a, i) =>
                            <tr key={i}>
                                <td>{i+1}</td>
                                <td>{a.name}</td>
                                <td>{a.count}</td>
                                <td>
                                    <button onClick={()=>{
                                        dispatch(upCount(a.id));
                                    }}> + </button>
                                </td>
                                <td>
                                    <button onClick={()=>{
                                        dispatch(delCart(a.id));
                                    }}> - </button>
                                </td>
                            </tr>
                        )
                    }
                </tbody>
            </Table>
        </div>
    )
}


export default Cart;